<template>
  <header>
    <div id="nav" :class="{ hideNav: !showNavbar }">
      <div id="logo">
        <img src="../assets/img/logo.png" alt="logo"/>
        <div class="logo_text">
          <span> nome </span>
          <span> cognome </span>
        </div>
      </div>
      <div id="menu">
        <ul class="inline_menu">
          <li
            v-for="(link, index) in links"
            :key="index"
            @click.stop="setActive(index)"
            :class="{ active: link.current }"
          >
            <router-link :to="link.url">
              {{ link.text }}
            </router-link>
          </li>
        </ul>
        <ul class="hamburger_menu hide">
          <li
            class="menuItem"
             v-for="(link, index) in links"
            :key="index"
            @click.stop="setActive(index)"
            :class="{ active: link.current }"
          >
             <router-link
             @click.native="toggleMenu" 
             :to="link.url">
              {{ link.text }}
            </router-link>
          </li>
        </ul>
        <div class="hamburger" @click="toggleMenu">
          <i class="my_hamburger fas fa-bars fa-2x"></i>
          <i class="my_close_hamburger fas fa-times fa-2x"></i>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  // preparo Header per essere esportato
  name: "Header",
  data() {
    return {
      links: [
        {
          text: "home",
          url: "/",
          current: false,
        },
        {
          text: "about",
          url: "/about",
          current: false,
        },
        {
          text: "outcomes",
          url: "/outcomes",
          current: false,
        },
      ],
      showNavbar: true,
      lastScrollPosition: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    // funzioneche aggiunge dinamicamente la classe active
    setActive(index) {
      this.links.forEach((link) => {
        link.current = false;
      });
      this.links[index].current = true;
    },
    // funzione che apre e chiude l'hamburger menu
    toggleMenu() {
      const menu = document.querySelector(".hamburger_menu"),
        closeIcon = document.querySelector(".my_close_hamburger"),
        menuIcon = document.querySelector(".my_hamburger");
      if (menu.classList.contains("show")) {
        menu.classList.remove("show");
        menu.classList.add("hide");
        closeIcon.style.display = "none";
        menuIcon.style.display = "block";
      } else {
        menu.classList.remove("hide");
        menu.classList.add("show");
        closeIcon.style.display = "block";
        menuIcon.style.display = "none";
      }
    },
    // funzione che nasconde il menu allo scrollDown e lo fa riapparire allo scrollUp
    onScroll() {
      // prendo il valore corrente di currentScrollPosition
      const currentScrollPosition =
        window.pageYOffset || document.documentElement.scrollTop;
      if (currentScrollPosition < 0) {
        return;
      }
      // Stoppa l'esecuzione della funzione se la differenza tra currentScrollPosition e lastScrollPosition è minore dell'offset (10)
      if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 10) {
        return;
      }
      this.showNavbar = currentScrollPosition < this.lastScrollPosition;
      // Setto il valore di currentScrollPosition con il valore di lastScrollPosition
      this.lastScrollPosition = currentScrollPosition;
    },
  },
};
</script>

<style lang="scss" scoped>
/* // importo gli stili */
@import "../assets/style/header.scss";
</style>