<template>
  <footer>
    <div class="footer_top" :class="{ hideFoot: !showFooter }">
      <div>
        <ul class="school">
          <li>
            <a
              href="https://www.tbs-education.fr/professeur/barbarossa-camilla/"
              target="_blank"
            >
              <img src="../assets/img/tbs-logoetbaseline.png" alt="logo tbs" />
            </a>
          </li>
        </ul>
      </div>
      <div>
        <ul>
          <li>
            <a href="https://www.linkedin.com/in/camilla-barbarossa-01422a40/" target="blank"><i class="fab fa-linkedin-in"></i></a>
          </li>
          <li>
            <a href="https://www.researchgate.net/profile/Camilla-Barbarossa" target="blank"><i class="fab fa-researchgate"></i></a>
          </li>
        </ul>
      </div>
    </div>
    <div class="footer_bottom">
      <div>
        <img src="../assets/img/logo.png" alt="" />
        <span> nome cognome </span>
      </div>
      <div>
        <span>
          powered by
          <a href="https://github.com/gabrisca" target="_blank">
            gabrisca
          </a></span
        >
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  // preparo Footer per essere esportato
  name: "Footer",
  data() {
    return {
      showFooter: false,
      lastScrollPosition: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    // funzione che nasconde il menu allo scrollDown e lo fa riapparire allo scrollUp
    onScroll() {
      // prendo il valore corrente di currentScrollPosition
      const currentScrollPosition = window.pageYOffset;
      if (currentScrollPosition < 10) {
        return;
      }
      // Stoppa l'esecuzione della funzione se la differenza tra currentScrollPosition e lastScrollPosition è minore dell'offset (10)
      if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 0) {
        return;
      }
      this.showFooter = currentScrollPosition > this.lastScrollPosition;
      // Setto il valore di currentScrollPosition con il valore di lastScrollPosition
      this.lastScrollPosition = currentScrollPosition;
    },
  },
};
</script>

<style lang="scss" scoped>
/* // importo gli stili */
@import "../assets/style/footer.scss";
</style>