import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter) // usa VueRouter   

import Home from '../views/Home.vue'
//import About from '../views/About.vue'
//import Outcomes from '../views/Outcomes.vue'

//creiamo la classe Router
const router = new VueRouter({
  mode: 'history', // tiene in memoria la navigazione nel browser
  routes: [{
      path: '/',
      name: 'Home', // serve per richiamare la rotta
      component: Home,
    },
    {
      path: '/about',
      name: 'About',
      component: () =>
        import("../views/About.vue")
    },
    {
      path: '/outcomes',
      name: 'Outcomes',
      component: () =>
        import("../views/Outcomes.vue")
    },
    {
      path: '/details/:id',
      name: 'Details',
      component: () =>
        import("../views/Details.vue")
    },
    {
      path: '/:pathMatch(.*)*', // tutte le altre rotte
      name: 'error404',
      component: () => import('../views/Error404.vue'),
    },
  ],
  scrollBehavior() { // return to the top of the page
    document.getElementById('app').scrollIntoView({
      behavior: 'smooth'
    });
  }
})

export default router