<template lang="">
  <div class="hero">
    <transition name="fade" mode="out-in">
      <div v-if="show" class="hero__content">
        <p class="intro-text">PhD Professor</p>
        <h1  class="big-text">Camilla Barbarossa</h1>
        <a href="https://www.tbs-education.com/" target="blank" class="button">Toulouse Business School</a>
        <div class="profile">
          <img src="../assets/img/profile.png" alt="profilo">
        </div>
      </div>
    </transition>
    
    <!--
    <video autoplay muted loop id="video-back" poster="../assets/img/bg-2.jpg">
      <source src="../assets/video/video-back.mp4" type="video/mp4" />
    </video>
    -->
  </div>
</template>
<script>
export default {
  name: "Hero",
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.slide();
  },
  methods: {
    slide() {
      setTimeout(() => {
        this.show = true;
      }, 400);
    },
  },
};
</script>
<style lang="scss" scoped>
/* // importo gli stili */
@import "../assets/style/variables.scss";
@import "../assets/style/mixin.scss";

.hero {
  @include flex($align-items: center);
  position: relative;
  height: 100vh;
  width: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.3)),
    no-repeat center center;
  background-image: url("../assets/img/wind.gif");
  background-size: cover;
  overflow: hidden;
  &:after {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background: rgb(0, 0, 0);
    opacity: 0.1;
  }
  .hero__content {
    width: 100%;
    max-width: 1350px;
