    position: relative;
    background: $hover-color;
    background: linear-gradient(
      90deg,
      $color-primary 0%,
      $color-main 35%,
      $hover-color 100%
    );
    h1,
    p {
      color: $brand-color;
    }
    .profile{
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      height: 300px;
      z-index: -1;
      img {
        height: 100%;
        filter: drop-shadow(30px 10px 4px #1f1f2271);
      }
    }
  }
