<script>
import Hero from "../components/Hero.vue";
import Skills from "../components/Skills.vue";

export default {
  name: "Home",
  components: {
    Hero,
    Skills,
  },
  data() {
    return {};
  },
  methods: {
    currentDate() {
      const current = new Date();
      switch (current.getDay()) {
        case 0:
          return `Sunday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 1:
          return `Monday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 2:
          return `Tuesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 3:
          return `Wednesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 4:
          return `Thursday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 5:
          return `Friday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 6:
          return `Saturday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        default:
          "";
          break;
      }
    },
    greeting() {
      var hours = new Date().getHours();
      console.log(hours);
      if (hours >= 6 && hours < 12) {
        return "Good Morning";
      } else if (hours >= 12 && hours < 18) {
        return "Good Afternoon";
      } else if (hours >= 18 && hours < 23) {
        return "Good Evening";
      } else {
        return "Welcome";
      }
    },
  },
};
